/* Chat Toggle Button */
.chat-toggle {
    display: flex;
    align-items: center;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
    padding: var(--space-md) var(--space-lg);
    background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
    color: white;
    border: none;
    border-radius: var(--radius-xl);
