/* Modern Product Detail Styles */
#detail {
    min-height: 100vh;
    background: var(--bg-secondary);
}

.detail {
    padding: 0;
}

/* Breadcrumb */
.breadcrumb {
    background: var(--bg-primary);
    padding: var(--space-md) 0;
    border-bottom: 1px solid var(--gray-200);
}

.breadcrumb-nav {
    display: flex;
    align-items: center;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
    font-size: 0.9rem;
}

.breadcrumb-nav a {
    color: var(--primary-color);
    text-decoration: none;
    transition: all var(--transition-normal);
}

.breadcrumb-nav a:hover {
    color: var(--primary-dark);
}

.breadcrumb-nav span {
    color: var(--text-tertiary);
}

/* Product Header */
.product-header {
    background: var(--bg-primary);
    padding: var(--space-lg) 0;
    border-bottom: 1px solid var(--gray-200);
}

.product-title h1 {
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: var(--space-md);
    line-height: 1.2;
}

.product-badges {
    display: flex;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
    flex-wrap: wrap;
}

.badge {
    padding: var(--space-xs) var(--space-sm);
    border-radius: var(--radius-md);
    font-size: 0.8rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.badge.new {
    background: var(--success-color);
    color: white;
}

.badge.discount {
    background: var(--error-color);
    color: white;
}

.badge.warranty {
    background: var(--accent-color);
    color: white;
}

/* Main Product Info */
.detail-info {
    padding: var(--space-2xl) 0;
    background: var(--bg-primary);
}

.detail-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: var(--space-2xl);
    gap: var(--space-2xl);
    align-items: start;
}

/* Image Gallery */
.image-gallery {
    display: flex;
    flex-direction: column;
    grid-gap: var(--space-md);
    gap: var(--space-md);
}

.main-image {
    position: relative;
    background: var(--bg-secondary);
    border-radius: var(--radius-xl);
    overflow: hidden;
    box-shadow: var(--shadow-lg);
    aspect-ratio: 1;
}

.main-image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: transform var(--transition-slow);
}

.main-image:hover img {
    transform: scale(1.05);
}

.image-zoom {
    position: absolute;
    top: var(--space-md);
    right: var(--space-md);
    background: rgba(0, 0, 0, 0.6);
    color: white;
    padding: var(--space-sm);
    border-radius: var(--radius-md);
    cursor: pointer;
    transition: all var(--transition-normal);
    opacity: 0;
}

.main-image:hover .image-zoom {
    opacity: 1;
}

.image-zoom:hover {
    background: var(--primary-color);
    transform: scale(1.1);
}

.thumbnail-images {
    display: flex;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
    overflow-x: auto;
    padding: var(--space-xs) 0;
}

.thumbnail {
    flex-shrink: 0;
    width: 80px;
    height: 80px;
    border-radius: var(--radius-md);
    overflow: hidden;
    cursor: pointer;
    border: 2px solid transparent;
    transition: all var(--transition-normal);
}

.thumbnail:hover,
.thumbnail.active {
    border-color: var(--primary-color);
    transform: scale(1.05);
}

.thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Product Tabs */
.product-tabs {
    padding: var(--space-2xl) 0;
    background: var(--bg-secondary);
}

.tabs-header {
    display: flex;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
    margin-bottom: var(--space-xl);
    border-bottom: 2px solid var(--gray-200);
    overflow-x: auto;
}

.tab-btn {
    padding: var(--space-md) var(--space-lg);
    background: transparent;
    border: none;
    color: var(--text-secondary);
    font-weight: 500;
    cursor: pointer;
    transition: all var(--transition-normal);
    border-bottom: 3px solid transparent;
    white-space: nowrap;
}

.tab-btn:hover,
.tab-btn.active {
    color: var(--primary-color);
    border-bottom-color: var(--primary-color);
    background: var(--bg-primary);
}

.tabs-content {
    background: var(--bg-primary);
    border-radius: var(--radius-xl);
    padding: var(--space-xl);
    box-shadow: var(--shadow-md);
}

.tab-panel {
    animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
}

/* Specifications */
.specifications-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: var(--space-md);
    gap: var(--space-md);
}

.spec-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--space-md);
    background: var(--bg-secondary);
    border-radius: var(--radius-md);
    border-left: 4px solid var(--primary-color);
}

.spec-label {
    font-weight: 600;
    color: var(--text-primary);
}

.spec-value {
    color: var(--text-secondary);
    text-align: right;
}

/* Features */
.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: var(--space-md);
    gap: var(--space-md);
}

.feature-item {
    display: flex;
    align-items: center;
    grid-gap: var(--space-md);
    gap: var(--space-md);
    padding: var(--space-md);
    background: var(--bg-secondary);
    border-radius: var(--radius-md);
    transition: all var(--transition-normal);
}

.feature-item:hover {
    background: var(--gray-100);
    transform: translateX(5px);
}

.feature-icon {
    color: var(--success-color);
    font-size: 1.2rem;
    font-weight: bold;
}

.feature-text {
    color: var(--text-primary);
    font-weight: 500;
}

/* Related Products */
.related-products {
    padding: var(--space-2xl) 0;
    background: var(--bg-primary);
}

.section-title {
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: var(--space-xl);
    color: var(--text-primary);
    position: relative;
}

.section-title::after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 4px;
    background: linear-gradient(90deg, var(--primary-color), var(--accent-color));
    border-radius: var(--radius-sm);
}

.related-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: var(--space-xl);
    gap: var(--space-xl);
}

.related-card {
    background: var(--bg-secondary);
    border-radius: var(--radius-xl);
    overflow: hidden;
    box-shadow: var(--shadow-md);
