    margin-bottom: var(--space-lg);
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.loading-overlay p {
    font-size: 1.1rem;
    font-weight: 500;
}

/* Marker Controls */
.marker-controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: var(--space-md);
    gap: var(--space-md);
    align-items: center;
    margin: var(--space-lg) 0;
    padding: var(--space-lg);
    background: var(--bg-secondary);
    border-radius: var(--radius-md);
}

.marker-controls label {
    font-weight: 500;
    color: var(--text-primary);
}

.marker-controls select {
    padding: var(--space-sm) var(--space-md);
    border: 1px solid var(--gray-300);
    border-radius: var(--radius-md);
    background: var(--bg-primary);
    color: var(--text-primary);
    font-size: 0.9rem;
    cursor: pointer;
}

.marker-controls input[type="file"] {
    padding: var(--space-xs) var(--space-sm);
    font-size: 0.9rem;
}

.marker-preview {
    margin-top: var(--space-xl);
    padding: var(--space-lg);
    background: var(--bg-secondary);
    border-radius: var(--radius-md);
    border: 1px solid var(--gray-200);
}

.marker-preview h4 {
    font-size: 1.1rem;
    color: var(--text-primary);
    margin-bottom: var(--space-sm);
}

.marker-preview p {
    color: var(--text-secondary);
    font-size: 0.9rem;
    margin-bottom: var(--space-md);
}

.marker-image-wrap {
    display: flex;
    justify-content: center;
    margin: var(--space-md) 0;
}

.download-marker {
    display: inline-block;
    padding: var(--space-sm) var(--space-md);
    background: var(--primary-color);
    color: white;
    text-decoration: none;
    border-radius: var(--radius-md);
    font-weight: 500;
    transition: all var(--transition-normal);
}

.download-marker:hover {
    background: var(--primary-dark);
    transform: translateY(-1px);
}

.aframe-wrapper {
    width: 100%;
    height: 100%;
    border-radius: var(--radius-md);
    overflow: hidden;
    background: #000; /* ensure black backdrop under video */
}

.aframe-wrapper a-scene {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: var(--radius-md);
}

.aframe-wrapper a-scene[arjs] {
    position: relative !important;
    background: transparent !important;
}

/* Ensure video element from AR.js is visible */
.aframe-wrapper video {
    width: 100% !important;
    height: 100% !important;
    object-fit: cover;
    border-radius: var(--radius-md);
}

/* Make sure canvas is visible */
.aframe-wrapper canvas {
    width: 100% !important;
    height: 100% !important;
    border-radius: var(--radius-md);
}

/* Hide A-Frame stats and VR button */
.aframe-wrapper .a-stats,
.aframe-wrapper .a-enter-vr {
    display: none !important;
}

/* A-Frame specific optimizations */
.aframe-wrapper a-scene[arjs] {
    position: relative !important;
}

.aframe-wrapper a-scene[arjs] > a-entity[camera] {
    z-index: 1;
}

/* Hide unnecessary A-Frame UI elements */
.aframe-wrapper .a-loader-title,
.aframe-wrapper .a-loader,
.aframe-wrapper .a-enter-ar,
.aframe-wrapper .a-enter-vr-button,
.aframe-wrapper .a-orientation-modal {
    display: none !important;
}

/* Ensure full viewport coverage */
.aframe-wrapper a-scene canvas {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 100% !important;
}

/* Ensure AR.js video element is visible under canvas */
.aframe-wrapper video, .aframe-wrapper .a-video {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 100% !important;
    object-fit: cover !important;
    z-index: 0 !important;
}

/* Place canvas above video */
.aframe-wrapper canvas {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 100% !important;
    z-index: 1 !important;
}

/* Ensure containers don't clip */
.ar-viewport.aframe-wrapper, .ar-vr-viewport.aframe-wrapper, .ar-vr-viewport {
    position: relative;
    overflow: hidden;
}

/* Loading states */
.ar-vr-viewport.loading {
    background: linear-gradient(45deg, #f0f0f0 25%, transparent 25%), 
                linear-gradient(-45deg, #f0f0f0 25%, transparent 25%), 
                linear-gradient(45deg, transparent 75%, #f0f0f0 75%), 
                linear-gradient(-45deg, transparent 75%, #f0f0f0 75%);
    background-size: 20px 20px;
    background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
    animation: loading-bg 1s linear infinite;
}

@keyframes loading-bg {
    0% { background-position: 0 0, 0 10px, 10px -10px, -10px 0px; }
    100% { background-position: 20px 20px, 20px 30px, 30px 10px, 10px 20px; }
}

/* Responsive Design */
@media (max-width: 768px) {
    .ar-viewer {
        padding: var(--space-lg);
        margin: var(--space-lg) 0;
    }
    
    .ar-viewer-header h2 {
        font-size: 1.5rem;
    }
    
    .mode-buttons {
        grid-template-columns: 1fr;
        grid-gap: var(--space-md);
        gap: var(--space-md);
    }
    
    .ar-vr-buttons {
        flex-direction: column;
        grid-gap: var(--space-md);
        gap: var(--space-md);
    }
    
    .ar-btn, .vr-btn {
        width: 100%;
        justify-content: center;
    }
    
    .components-list {
        grid-template-columns: 1fr;
    }
    
    .ar-vr-controls {
        flex-direction: column;
        grid-gap: var(--space-md);
        gap: var(--space-md);
        align-items: stretch;
    }
    
    .view-controls {
        justify-content: center;
    }
    
    .ar-vr-instructions {
        position: static;
        margin-top: var(--space-lg);
        max-width: none;
    }
    
    .ar-viewport, .vr-viewport {
        height: 300px;
    }
    
    .marker-controls {
        flex-direction: column;
        align-items: stretch;
    }
    
    .marker-controls label {
        margin-bottom: var(--space-xs);
    }
    
    .marker-controls select,
    .marker-controls input {
        margin-bottom: var(--space-sm);
    }
}

@media (max-width: 480px) {
    .ar-viewer-header h2 {
        font-size: 1.3rem;
    }
    
    .mode-btn {
        padding: var(--space-md);
    }
    
    .mode-icon {
        font-size: 1.5rem;
    }
    
    .ar-btn, .vr-btn {
        padding: var(--space-md) var(--space-lg);
        font-size: 1rem;
    }
    
    .control-btn {
        width: 40px;
        height: 40px;
        font-size: 1rem;
    }
    
    .ar-vr-instructions {
        padding: var(--space-md);
    }
    
    .ar-vr-instructions h4 {
        font-size: 0.9rem;
    }
    
    .ar-vr-instructions li {
        font-size: 0.8rem;
    }
}

/* Touch device optimizations */
@media (hover: none) and (pointer: coarse) {
    .mode-btn:hover,
    .ar-btn:hover,
    .vr-btn:hover,
    .control-btn:hover {
        transform: none;
    }
    
    .mode-btn:active,
    .ar-btn:active,
    .vr-btn:active {
        transform: scale(0.95);
    }
    
    .control-btn:active {
        transform: scale(0.9);
    }
}

