/* Global AI Chatbot Styles */
.global-ai-chatbot {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 10000;
    font-family: 'Inter', sans-serif;
}

/* Chat Toggle Button */
.chat-toggle {
    display: flex;
    align-items: center;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
    padding: var(--space-md) var(--space-lg);
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border: none;
    border-radius: var(--radius-xl);
    cursor: pointer;
    box-shadow: 0 8px 32px rgba(102, 126, 234, 0.3);
    transition: all var(--transition-normal);
    font-weight: 600;
    font-size: 0.95rem;
    position: relative;
    overflow: hidden;
}

.chat-toggle::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
}

.chat-toggle:hover::before {
    left: 100%;
}

.chat-toggle:hover {
    transform: translateY(-3px);
    box-shadow: 0 12px 40px rgba(102, 126, 234, 0.4);
}

.chat-icon {
    font-size: 1.3rem;
    animation: pulse 2s infinite;
}

.chat-text {
    font-size: 0.9rem;
}

.chat-badge {
    background: #ff4757;
    color: white;
    font-size: 0.7rem;
    padding: 2px 6px;
    border-radius: 10px;
    font-weight: 700;
    animation: bounce 1s infinite;
}

@keyframes pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.1); }
}

@keyframes bounce {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-3px); }
}

/* Chat Window */
.chat-window {
    position: absolute;
    bottom: 80px;
    right: 0;
    width: 420px;
    height: 650px;
    background: var(--bg-primary);
    border-radius: var(--radius-xl);
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
    border: 1px solid var(--gray-200);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transform: translateY(20px) scale(0.95);
    opacity: 0;
    transition: all var(--transition-normal);
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
}

.global-ai-chatbot.open .chat-window {
    transform: translateY(0) scale(1);
    opacity: 1;
}

/* Chat Header */
.chat-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--space-lg);
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    position: relative;
}

.chat-header::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
}

.bot-info {
    display: flex;
    align-items: center;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
}

.bot-avatar {
    width: 45px;
    height: 45px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.3rem;
    border: 2px solid rgba(255, 255, 255, 0.3);
}

.bot-details h3 {
    margin: 0;
    font-size: 1.1rem;
    font-weight: 700;
}

.status {
    font-size: 0.8rem;
    opacity: 0.9;
    display: flex;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
}

.status::before {
    content: '●';
    color: #4ade80;
    animation: pulse 2s infinite;
}

.close-btn {
    background: none;
    border: none;
    color: white;
    font-size: 1.3rem;
    cursor: pointer;
    padding: var(--space-xs);
    border-radius: 50%;
    transition: all var(--transition-normal);
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.close-btn:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: rotate(90deg);
}

/* Chat Messages */
.chat-messages {
    flex: 1 1;
    padding: var(--space-lg);
    overflow-y: auto;
    background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
    display: flex;
    flex-direction: column;
    grid-gap: var(--space-md);
    gap: var(--space-md);
}

.message {
    display: flex;
    margin-bottom: var(--space-sm);
}

.message.user {
    justify-content: flex-end;
}

.message.bot {
    justify-content: flex-start;
}

.message-content {
    max-width: 85%;
    padding: var(--space-md) var(--space-lg);
    border-radius: var(--radius-lg);
    position: relative;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.message.user .message-content {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border-bottom-right-radius: var(--radius-sm);
}

.message.bot .message-content {
    background: var(--bg-primary);
    color: var(--text-primary);
    border: 1px solid var(--gray-200);
    border-bottom-left-radius: var(--radius-sm);
}

.message-text {
    font-size: 0.9rem;
    line-height: 1.6;
    margin-bottom: var(--space-xs);
}

.message-text strong {
    font-weight: 600;
}

.message-time {
    font-size: 0.7rem;
    opacity: 0.7;
    text-align: right;
}

.message.bot .message-time {
    text-align: left;
}

/* Typing Indicator */
.typing-indicator {
    display: flex;
    grid-gap: 4px;
    gap: 4px;
    padding: var(--space-sm) 0;
}

.typing-indicator span {
    width: 8px;
    height: 8px;
    background: var(--gray-400);
    border-radius: 50%;
    animation: typing 1.4s infinite ease-in-out;
}

.typing-indicator span:nth-child(1) {
    animation-delay: -0.32s;
}

.typing-indicator span:nth-child(2) {
    animation-delay: -0.16s;
}

@keyframes typing {
    0%, 80%, 100% {
        transform: scale(0.8);
        opacity: 0.5;
    }
    40% {
        transform: scale(1);
        opacity: 1;
    }
}

/* Quick Questions */
.quick-questions {
    padding: var(--space-lg);
    background: var(--bg-primary);
    border-top: 1px solid var(--gray-200);
}

.quick-questions h4 {
    margin: 0 0 var(--space-md) 0;
    font-size: 0.9rem;
    color: var(--text-secondary);
    font-weight: 600;
}

.question-buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
}

.question-btn {
    padding: var(--space-sm) var(--space-md);
    background: var(--bg-secondary);
    border: 1px solid var(--gray-200);
    border-radius: var(--radius-md);
    cursor: pointer;
    transition: all var(--transition-normal);
    font-size: 0.8rem;
    color: var(--text-primary);
    text-align: left;
    line-height: 1.3;
}

.question-btn:hover {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border-color: transparent;
    transform: translateY(-1px);
}

/* Chat Input */
.chat-input {
    display: flex;
    padding: var(--space-lg);
    background: var(--bg-primary);
    border-top: 1px solid var(--gray-200);
    grid-gap: var(--space-sm);
    gap: var(--space-sm);
}

.message-input {
    flex: 1 1;
    padding: var(--space-sm) var(--space-md);
    border: 2px solid var(--gray-300);
    border-radius: var(--radius-lg);
    outline: none;
    font-size: 0.9rem;
    transition: all var(--transition-normal);
    background: var(--bg-secondary);
}

.message-input:focus {
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
    background: var(--bg-primary);
}

.send-btn {
    padding: var(--space-sm);
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border: none;
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-normal);
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 45px;
    height: 45px;
}

.send-btn:hover:not(:disabled) {
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.send-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
}

/* Responsive Design */
@media (max-width: 768px) {
    .global-ai-chatbot {
        bottom: 10px;
        right: 10px;
    }
    
    .chat-window {
        width: calc(100vw - 20px);
        height: 550px;
        right: -10px;
    }
    
    .chat-toggle {
        padding: var(--space-sm) var(--space-md);
    }
    
    .chat-text {
        display: none;
    }
    
    .message-content {
        max-width: 90%;
    }
    
    .quick-questions {
        padding: var(--space-md);
    }
    
    .question-buttons {
        grid-template-columns: 1fr;
        grid-gap: var(--space-xs);
        gap: var(--space-xs);
    }
    
    .question-btn {
        padding: var(--space-xs) var(--space-sm);
        font-size: 0.75rem;
    }
}

@media (max-width: 480px) {
    .chat-window {
        width: calc(100vw - 10px);
        height: 500px;
        right: -5px;
    }
    
    .chat-messages {
        padding: var(--space-md);
    }
    
    .message-content {
        padding: var(--space-sm) var(--space-md);
    }
    
    .message-text {
        font-size: 0.85rem;
    }
    
    .chat-input {
        padding: var(--space-md);
    }
    
    .message-input {
        font-size: 0.85rem;
    }
}

/* Scrollbar Styling */
.chat-messages::-webkit-scrollbar {
    width: 6px;
}

.chat-messages::-webkit-scrollbar-track {
    background: var(--gray-100);
    border-radius: 3px;
}

.chat-messages::-webkit-scrollbar-thumb {
    background: var(--gray-400);
    border-radius: 3px;
}

.chat-messages::-webkit-scrollbar-thumb:hover {
    background: var(--gray-500);
}

/* Animation for opening */
@keyframes slideInUp {
    from {
        transform: translateY(100%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.global-ai-chatbot.open .chat-window {
    animation: slideInUp 0.3s ease-out;
}

